.banner{

    position: relative;

    margin-left: auto;

    // background-color: #303030;

    // overflow: hidden;

    // width: calc(45% + 270px);
    width: 1295px;
    height: 100%;

    .svg_banner-back{

        position: relative;

        width: fit-content;
        height: fit-content;

        top: -5px;

    }

    .description{

        position: absolute;

        width: fit-content;

        z-index: 2;

        display: flex;
        flex-direction: column;

        bottom: 50%;
        left: calc(50% - 180px);
        // right: 130px;
        
        &__title{

            font-family: 'Grand Hotel';
            font-size: 125px;

            color: #fff;

        }

        &__subtitle{

            font-family: 'Montserrat';
            font-size: 30px;

            color: #fff;

            i{
                color: #D47FFF;
            }
        }

    }

    .menu_header{

        position: absolute;
        display: flex;

        font-family: 'Grand Hotel';

        font-size: 48px;

        right: 20%;
        top: 30px;

        a{
            color: #fff;
            text-decoration: none;

            margin-right: 60px;

            &:last-child{
                margin-right: 0;
            }

        }

        z-index: 1;

    }
    
    .parallax{

        position: absolute;

        width: 100%;
        height: 100%;

        z-index: 1;

        .svg{

            position: absolute;

            &-vue{
                left: 24%;
                bottom: 29%;
            }

            &-react{
                bottom: 50%;
                left: 10%;
            }

            &-github{
                top: 13%;
                left: 27%;
            }

            &-gitlab{
                bottom: 11%;
                left: 34%;
            }

            &-docker{
                top: 15%;
                right: 26%;
            }

            &-node{
                right: 10%;
                top: 48%;
            }

            &-pug{
                right: 15%;
                bottom: 20%;
            }

        }

        &__group{

            display: flex;
            position: absolute;

            left: calc(50% - -5px);
            bottom: 25%;

            .svg{
                position: relative;
            }
            
        }

    }


}