.portfolio{

    display: flex;
    flex-direction: column;

    align-items: center;

    margin-top: 200px;

    z-index: 2;

    &__cards{

        display: grid;

        margin-top: 100px;
        
        grid-gap: 30px;
        grid-template-columns: repeat(3, 1fr);
        justify-content: center;

    }

}