.footer{

    display: flex;
    flex-direction: column;

    z-index: 1;
    // transform: scale(-1,1);

    margin-top: 150px;

    &__inner{

        display: grid;
        grid-template-columns: repeat(2,1fr);

        background: #303030;

    }

    &__contact{

        display: flex;
        flex-direction: column;

        align-items: center;

    }

    .avatar-profile{

        width: 600px;
        height: 600px;

        border-radius: 50%;

        background-color: #AE43C0;

        padding: 15px;

        img{

            display: block;

            object-fit: cover;
            object-position: center;

            width: 100%;
            height: 100%;

            max-width: 100%;
            max-height: 100%;

            border-radius: 50%;

        }

        &__circle{



        }

    }

}