
@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&family=Montserrat&display=swap');

body {

  font-family: 'MontserratRegular';

	height: 100vh;

  margin: 0;
  overflow-x: hidden;

}

html{
  position: relative;
}

.App{

  display: flex;
  flex-direction: column;

  &__gradient{

    position: fixed;
    width: 100%;
    height: 100%;
  
    // Градиент на заднем фоне
    background: linear-gradient(-45deg, #D47FFF, #5736DF,#AE43C0,#2396ED);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;

  }

}

.title{

  $title:&;

  &_type-1{

    display: flex;
    position: relative;

    align-items: center;
    justify-content: center;

    width: fit-content;

    #{$title}__text{

      position: absolute;

      font-family: 'Grand Hotel';
      font-size: 60px;

      color: #fff;

      transform: rotate(-17deg);

      z-index: 1;

    }

  }

  &_type-2{

    font-family: 'Grand Hotel';
    font-size: 125px;

    color: #fff;

  }

}

.card{

  $card:&;

  &_type-1{

    --transition: 0.8s;

    height: fit-content;
    width: fit-content;

    margin: auto;

    position: relative;
    background-color: #303030;

    min-width: 190px;
    min-height: 190px;

    border-radius: 20px;
    box-shadow: 0 4px 12px #D47FFF;

    transition: var(--transition);

    &:hover {

      min-height: 370px;
      min-width: 350px;

      #{$card}__avatar{
        height: 150px;
        transition: var(--transition);
      }

      #{$card}__avatar-image{
        border-radius: 25%;
        width: 190px;
        height: 190px;
      }

      #{$card}__info{
        top: 150px;
        height: 205px;
        transition: var(--transition);
      }

    }

    &::after #{$card}__avatar {

      height: 100px;
      transition: var(--transition);

    }

    #{$card}__avatar-image{

      position: absolute;
      right: 50%;
      top: -50px;
      transform: translateX(50%);
      border-radius: 50%;
      box-shadow: 2px 3px 10px #5736DF;
      background-size: cover;
      align-items: center;
      justify-items: center;
      justify-self: center;
      width: 70%;
      height: 70%;
      transition: var(--transition);
      overflow: hidden;

      img{

        height: 100%;
        position: absolute;
        background-position: center;
        right: 50%;
        transform: translateX(50%);
        background-size: cover;

      }

    }

    #{$card}__info{

      position: absolute;
      top: 105px;
      width: 100%;
      height: 30%;
      display: flex;
      text-align: center;
      justify-content: center;
      overflow: hidden; /*prueba*/
      transition: var(--transition);

      #{$card}__info-wrap{

        position: absolute;
        height: 200px;
        width: 100%;
        transition: var(--transition);

      }

    }

  }

  &_type-2{

    display: flex;
    flex-direction: column;

    width: fit-content;

    #{$card}__circle{

      display: flex;

      position: relative;

      width: 280px;
      height: 280px;

      border: 10px solid #D47FFF;

      border-radius: 50%;

      .svg{

        position: absolute;

        width: fit-content;
        height: fit-content;

        &-react{

          margin: auto;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;

        }

        &-redux{

          margin: auto;
          left: 0;
          right: -40%;
          top: 0;
          bottom: -60%;

        }

        &-ts{

          margin: auto;
          left: 0;
          right: 40%;
          top: 0;
          bottom: 60%;

        }

        &-js{
          margin: auto;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
        }

        &-pug{
          margin: auto;
          left: 0;
          right: 0;
          top: -63%;
          bottom: 0;
        }

        &-scss{
          margin: auto;
          left: 0;
          right: 0;
          top: 63%;
          bottom: 0;
        }

        &-css{
          margin: auto;
          left: 0;
          right: -63%;
          top: 0;
          bottom: 0;
        }

        &-html{
          margin: auto;
          left: 0;
          right: 63%;
          top: 0;
          bottom: 0;
        }

        &-wordpress{
          margin: auto;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
        }

        &-rest{
          margin: auto;
          left: 0;
          right: 0;
          top: 70%;
          bottom: 0;
        }

        &-graphql{
          margin: auto;
          left: 0;
          right: 0;
          top: -74%;
          bottom: 0;
        }

        &-docker{
          margin: auto;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
        }

        &-gitlab{
          margin: auto;
          left: 0;
          right: 0;
          top: -40%;
          bottom: 0;
        }

        &-github{
          margin: auto;
          left: 0;
          right: -45%;
          top: 25%;
          bottom: 0;
        }

        &-git{
          margin: auto;
          left: 0;
          right: 45%;
          top: 25%;
          bottom: 0;
        }

      }

    }

    #{$card}__title{

      font-family: 'Grand Hotel';
      
      font-weight: 400;
      font-size: 48px;

      line-height: 60px;

      color: #fff;

    }

    #{$card}__info{

      display: flex;
      text-align: center;

      margin: auto;
      margin-top: 15px;

    }

  }

  &_type-3{

    --color: #3c3163;
    --transition-time: 0.5s;

    font-family: 'Heebo';
    --bg-filter-opacity: 0.5;
    background-image: linear-gradient(rgba(0,0,0,var(--bg-filter-opacity)),rgba(0,0,0,var(--bg-filter-opacity))), var(--bg-img);
    height: 20em;
    width: 15em;
    font-size: 1.5em;
    color: white;
    border-radius: 1em;
    padding: 1em;
    /*margin: 2em;*/
    display: flex;
    align-items: flex-end;
    background-size: cover;
    background-position: center;
    box-shadow: 0 0 5em -1em black;
    transition: all, var(--transition-time);
    position: relative;
    overflow: hidden;
    border: 10px solid #ccc;
    text-decoration: none;

    &:hover{

      transform: rotate(0);

      color: var(--color);

      .tags .tag {
        background: var(--color);
        color: white;
      }

      &::before, &::after{
        transform: scale(1);
      }

    }

    &:before,&::after{
      content: '';
      transform: scale(0);
      transform-origin: top left;
      border-radius: 50%;
      position: absolute;
      left: -50%;
      top: -50%;
      z-index: -5;
      transition: all, var(--transition-time);
      transition-timing-function: ease-in-out;
    }

    &::before{
      background: #ddd;
      width: 250%;
      height: 250%;
    }

    &::after{
      background: white;
      width: 200%;
      height: 200%;
    }

    h1{
      margin: 0;
      font-size: 1.5em;
      line-height: 1.2em;
    }

    p{
      font-size: 0.75em;
      font-family: 'Open Sans';
      margin-top: 0.5em;
      line-height: 2em;
    }

    .tags{

      display: flex;

      .tag{

        font-size: 0.75em;
        background: rgba(255,255,255,0.5);
        border-radius: 0.3rem;
        padding: 0 0.5em;
        margin-right: 0.5em;
        line-height: 1.5em;
        transition: all, var(--transition-time);

      }

    }

    .date{
      position: absolute;
      top: 0;
      right: 0;
      font-size: 0.75em;
      padding: 1em;
      line-height: 1em;
      opacity: .8;
    }

    #{$card}{

      &__num{

        font-size: 3em;
        margin-bottom: 1.2rem;
        margin-left: 1rem;

      }

      &__link{



      }

    }

  }

}

.btn{

  $btn:&;

  &_type-1{

    position: relative;
    display: inline-block;

    padding: 25px 30px;

    color: #D47FFF;

    text-decoration: none;
    text-transform: uppercase;

    transition: 0.5s;
    letter-spacing: 4px;
    overflow: hidden;

    font-family: 'Montserrat';
    font-size: 30px;

    width: fit-content;

    &#{$btn}--active {  
      background: #D47FFF;
      color: #050801;
      box-shadow: 0 0 5px #D47FFF,
                  0 0 25px #D47FFF,
                  0 0 50px #D47FFF,
                  0 0 20px #D47FFF;
      -webkit-box-reflect:below 1px linear-gradient(transparent, #0005);
    }

    &:hover{
      @extend .btn--active;
    }

    &:nth-child(1){
        filter: hue-rotate(270deg);
    }
    &:nth-child(2){
        filter: hue-rotate(110deg);
    }

    span{

      display: block;
      position: absolute;

      &:nth-child(1){
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background: linear-gradient(90deg,transparent,#D47FFF);
        animation: animate1 1s linear infinite;
      }

      &:nth-child(2){
        top: -100%;
        right: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(180deg,transparent,#D47FFF);
        animation: animate2 1s linear infinite;
        animation-delay: 0.25s;
      }

      &:nth-child(3){
        bottom: 0;
        right: 0;
        width: 100%;
        height: 2px;
        background: linear-gradient(270deg,transparent,#D47FFF);
        animation: animate3 1s linear infinite;
        animation-delay: 0.50s;
      }

      &:nth-child(4){
        bottom: -100%;
        left: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(360deg,transparent,#D47FFF);
        animation: animate4 1s linear infinite;
        animation-delay: 0.75s;
      }

    }

    @keyframes animate1{
        0%{
            left: -100%;
        }
        50%,100%{
            left: 100%;
        }
    }
    @keyframes animate2{
        0%{
            top: -100%;
        }
        50%,100%{
            top: 100%;
        }
    }
    @keyframes animate3{
        0%{
            right: -100%;
        }
        50%,100%{
            right: 100%;
        }
    }
    @keyframes animate4{
        0%{
            bottom: -100%;
        }
        50%,100%{
            bottom: 100%;
        }
    }

  }

}

.form{

  $form:&;

  &_type-1{

    position: relative;

    border: 2px solid #6b6b6b;
    max-width: 50vw;

    transition: 3.5s all ease;

    text-align: center;

    margin-left: auto;
    margin-right: auto;

    &:hover{

      border: 2px solid #ffffff;
      box-shadow: 0 0 0.2rem #ffffff, 0 0 0.8rem #5736DF, inset 0 0 0.2rem #ffffff, inset 0 0 0.8rem #5736DF;

      #{$form}__svg-mail{

        #envelopepath{
          animation: formanimation 3.2s linear forwards, formopacity 0.5s linear forwards, envelopecolor 2.7s linear infinite;
          animation-delay: 0s, 0s, 3s;
        }

      }

    }

    #{$form}{

      &__svg-mail{

        position: relative;
        width: 50%;
        padding-left: 1.2%;
        margin-top: 8%;

        #envelopepath0{
          fill: none;
          stroke: #6b6b6b;
          stroke-width: 1;
          stroke-miterlimit: 10;
        }
  
        #envelopepath{
          filter: url(#blueglow);
          fill: none;
          stroke-width: 1;
          stroke-miterlimit: 10;
          stroke-dasharray: 1200;
          stroke-dashoffset: 1200;
          stroke: #ffffff;
          opacity: 0;
        }

      }

      &__action{

        padding-left: 5%;
        padding-right: 5%;

      }

    }

  }

}

.robocat{

  transform: scale(-1, 1);

  width: fit-content;
  height: fit-content;

  z-index: 1;

}

.padding-page{

  padding: 0 100px;

}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
