.development{

    display: flex;
    flex-direction: column;

    position: relative;

    margin-top: 200px;

    z-index: 1;

    &__inner{

        display: flex;
        flex-direction: column;

        background: #303030;

        &-head{
            display: flex;
            z-index: 1;
            margin-top: -150px;
        }

    }

    &__tabs{

        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;

        margin-top: 100px;
        justify-items: center;
        
    }

    &-info{

        display: flex;
        flex-direction: column;
        align-items: center;

        flex-grow: 1;

        &__head{
            display: flex;
            position: relative;
        }

    }

    .technologies{

        &-info{

            &__subtitle{

                font-family: 'Montserrat';

                font-style: normal;
                font-weight: 400;
                font-size: 36px;

                line-height: 44px;

                color: #fff;

            }

        }

        &__inner{

            display: grid;

            grid-gap: 30px;
            grid-template-columns: repeat(5, 1fr);

            margin-top: 70px;

            & > *:nth-child(2n) {

                .card__circle{

                    border-color: #5736DF;
                    
                }

            }

        }

    }

    .advantages{

        display: grid;
        grid-template-columns: repeat(4,1fr);

        gap: 50px;

        justify-items: center;
        margin: auto;

        margin-top: 100px;

    }

    .svg {

        &-dev{

            z-index: 1;
            text-decoration: none;
    
            &_reverse{
                transform: scale(-1,-1);
            }
    
        }

        &-code{

            position: absolute;
            top: -40px;
            left: calc(-134px - 90px);

        }

        &-paint{

            position: absolute;
            bottom: -40px;
            right: calc(-134px - 90px);

        }

    }

    .robocat{
        // position: absolute;

        // left: 0;
        // top: 0;
        z-index: 2;
    }

}